@charset "UTF-8"; 

html,body {
	width: 100%;
	height: 100%;
	margin: 0 auto;
	padding: 0; 
	background-color: #ece060;
	overflow: hidden;
}
main {
	display: -webkit-box;
	display: -webkit-flex;
	display: -moz-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	-webkit-flex-direction: column;
	   -moz-box-orient: vertical;
	   -moz-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	-webkit-box-pack: justify;
	-webkit-justify-content: space-between;
	   -moz-box-pack: justify;
	    -ms-flex-pack: justify;
	        justify-content: space-between;
	height: 100%;
}
#app {
  position: relative;
  -webkit-box-sizing: border-box;
     -moz-box-sizing: border-box;
          box-sizing: border-box;
  border: #414042 solid 3px;
  background: #fff;
  margin: 0 auto;
  margin-top: 30px;
	width: 80vw;
	height: 80vw; 
}

.num {
  position: absolute;
  top: -27px;
}
.level {
	position: absolute;
	top: -27px;
	right: 0;
}
.snake-controller {
	display: -webkit-box;
	display: -webkit-flex;
	display: -moz-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-justify-content: space-around;
	    -ms-flex-pack: distribute;
	        justify-content: space-around;
	-webkit-box-align: end;
	-webkit-align-items: flex-end;
	   -moz-box-align: end;
	    -ms-flex-align: end;
	        align-items: flex-end;
	position: relative;
	width: 100%;
	padding-bottom: 10px;
}

.snake-switch {
	width: 50px; 
	height: 53px; 
	background: url(data:image/png;base64,) 0 0 no-repeat; 
	background-size: 100% 100%;
	border: 0 none; 
	-moz-appearance: none; 
	     appearance: none; 
	-webkit-appearance: none; 
	outline: 0; 
}

.snake-trigger {
	width: 50px; 
	height: 50px; 
	background: url(data:image/png;base64,) 0 0 no-repeat; 
	background-size: 100% 100%; 
	border: 0 none; 
	-moz-appearance: none; 
	     appearance: none; 
	-webkit-appearance: none; 
	outline: 0; 
}
.snake-trigger:checked {
	background-image: url(data:image/png;base64,);
}
.snake-direction-box {
	display: -webkit-box;
	display: -webkit-flex;
	display: -moz-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	-webkit-flex-direction: column;
	   -moz-box-orient: vertical;
	   -moz-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	-webkit-box-align: center;
	-webkit-align-items: center;
	   -moz-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
}
.snake-direction {
	display: -webkit-box;
	display: -webkit-flex;
	display: -moz-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-flex-wrap: wrap;
	    -ms-flex-wrap: wrap;
	        flex-wrap: wrap;
	width: 160px; 
	height: 160px;
	border: 2px solid #414042; 
	background-color: #414042; 
	border-radius: 100%; 
	overflow: hidden; 
	-ms-transform: rotate(45deg); 
	    transform: rotateZ(45deg);
	-webkit-transform: rotateZ(45deg); 
}

.snake-up, .snake-right, .snake-down, .snake-left {
	position: relative; 
	-webkit-box-sizing: border-box; 
	   -moz-box-sizing: border-box; 
	        box-sizing: border-box;
	width: 80px; 
	height: 80px; 
	background-color: #ddd;
}

.snake-up::after, 
.snake-right::after, 
.snake-down::after, 
.snake-left::after {
	content: ''; 
	position: absolute; 
	left: 40px; 
	top: 40px; 
	width: 10px; 
	height: 10px; 
	border-width: 2px 0 0 2px; 
	border-color: #414042; 
	border-style: solid; 
	-webkit-transform-origin: left top; 
	    -ms-transform-origin: left top; 
	        transform-origin: left top;
}
.snake-right::after {
	-webkit-transform: rotate(90deg);
	    -ms-transform: rotate(90deg);
	        transform: rotate(90deg);
}
.snake-down::after {
	-webkit-transform: rotate(180deg);
	    -ms-transform: rotate(180deg);
	        transform: rotate(180deg);
}
.snake-left::after {
	-webkit-transform: rotate(270deg);
	    -ms-transform: rotate(270deg);
	        transform: rotate(270deg);
}

.snake-direction.up .snake-up, 
.snake-direction.right .snake-right, 
.snake-direction.down .snake-down, 
.snake-direction.left .snake-left 
{
	background-color: rgba(188, 188, 188, .7); 
}

.snake-speed {
	width: 120px; 
	height: 4px;
	margin: 20px 0;
	border-radius: 2px; 
  background-color: #fee;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
	-webkit-box-shadow: inset 1px 1px 3px -1px rgba(0, 0, 0, .3);
	        box-shadow: inset 1px 1px 3px -1px rgba(0, 0, 0, .3); 
	position: relative;
  z-index: 1;
}
.snake-speed:focus {
  outline: none;
}
.snake-speed::-webkit-slider-thumb {
  -webkit-appearance: none;
          appearance: none;
  width: 10px; 
	height: 10px; 
	border-radius: 100%; 
	-webkit-box-shadow: 0 2px 4px rgba(0, 0, 0, .3); 
	        box-shadow: 0 2px 4px rgba(0, 0, 0, .3); 
	background-color: #fff; 
}

